import React from 'react'
import Layout from "../components/Layout.js";
import ExternalLink from "../components/ExternalLink";
import projectData from "../data/projects.json";
import languageData from "../data/languages.json";
import global from "../data/global.json";

const ProjectLangIcon = props => {
	const lang = languageData[props.type][props.name];
	return (
		<img src={global.paths.img+props.type+"/"+lang.img} className="projectIcon" title={lang.displayName} />
	)
}

const ProjectCard = props => (
	<div className={"projectCard"+ (props.active ? " projectActive" : "")} onClick={props.onClick}>
		<p className="projectTitle">{props.project.title}</p>
		<img className="projectImg" src={global.paths.img+"projects/"+props.project.img} />
		<div className="projectDesc">{props.project.desc}
			<div className="projectInfo">
				<p className="projectInfoLabel">Languages:<br></br>{props.project.languages.map(language => <ProjectLangIcon type="languages" name={language} />)}</p>
				<p className="projectInfoLabel">Frameworks:<br></br>{props.project.frameworks.map(framework => <ProjectLangIcon type="frameworks" name={framework} />)}</p>
			</div>
			<br></br>
			<div className="projectButtonContainer">
				<ExternalLink className="projectButton" href={props.project.demo}>VIEW</ExternalLink>
				{(props.project.source=="" ?
					<div className="projectButton projectButtonDisabled">CODE</div>
					:
					<ExternalLink className="projectButton" href={props.project.source}>CODE</ExternalLink>
				)}
			</div>
		</div>
		<div className="projectSeparator"></div>
	</div>
)

class ProjectContainer extends React.Component {
	constructor(props) {
		super(props);
		this.state = {
			activeProject: null
		}
	}

	handleClick(title) {
		if(window.innerWidth > global.constants.mobileWidth) {
			this.setState({activeProject: title});
		}
	}

	render() {
		return (
			<>
				{
					(this.state.activeProject) ? <div className="projectFade" onClick={() => {this.handleClick(null)}}></div> : ""
				}
				<div className="projectContainer">
					{projectData.map(project => (
						<ProjectCard project={project} active={project.title==this.state.activeProject} key={project.title} onClick={() => {this.handleClick(project.title)}} />
					))}
				</div>
			</>
		)
	}
}

const Projects = () => (
	<Layout page="Projects">
		<ProjectContainer />
		<style jsx global>{`
			.projectContainer {
				box-sizing: border-box;
				width: 100%;
				padding: 50px 30px 50px 30px;
				display: flex;
				justify-content: center;
				align-items: center;
				flex-wrap: wrap;
			}
			.projectCard {
				box-sizing: border-box;
				border-radius: 20px;
				margin: 30px;
				width: 350px;
				height: 435px;
				padding: 30px;
				display: inline-block;
				background-color: #fff;
				box-shadow: 10px 10px 10px #ccc;
				cursor: pointer;
				position: relative;
				bottom: 0px;
				will-change: bottom, box-shadow;
				transition: bottom 0.2s, box-shadow 0.2s;
				transition-timing-function: ease-out;
			}
			.projectTitle {
				font-size: 28px;
				position: relative;
				margin: 0 0 30px 0;
			}
			.projectDesc {
				display: none;
				font-size: 16px;
			}
			.projectImg {
				width: 350px;
				position: absolute;
				left: 0px;
				bottom: 0px;
				border-radius: 0 0 20px 20px;
			}
			.projectInfo {
				display: flex;
				align-items: center;
				justify-content: center;
			}
			.projectInfoLabel {
				margin-top: 40px;
				flex-grow: 1;
			}
			.projectIcon {
				width: 48px;
				height: 48px;
				position: relative;
				top: 10px;
			}
			.projectButtonContainer {
				display: flex;
				align-items: center;
				justify-content: center;
			}
			.projectButton {
				box-sizing: border-box;
				background-color: ${global.colors.accentDark};
				color: ${global.colors.textLight};
				padding: 20px;
				flex-grow: 1;
				text-align: center;
				font-size: 24px;
				border-radius: 10px;
				margin-top: 30px;
				margin-left: 10px;
				margin-right: 10px;
				position: relative;
				transform: translateY(0);
				box-shadow: 5px 5px 10px #aaa;
				will-change: transform, box-shadow;
				transition: transform 0.2s, box-shadow 0.2s;
			}
			.projectButton:not(.projectButtonDisabled):hover {
				background-color: ${global.colors.accentLight};
				transform: translateY(-5px);
				box-shadow: 10px 10px 10px #aaa;
			}
			.projectButton:not(.projectButtonDisabled):active {
				transform: translateY(5px);
				box-shadow: 0px 0px 10px #aaa;
			}
			.projectButtonDisabled {
				cursor: not-allowed;
				box-shadow: unset;
				background-color: ${global.colors.disabled};
			}
			.projectSeparator {
				display: none;
			}

			@media only screen and (min-width: ${global.constants.mobileWidth + 1}px) {
				.projectCard:not(.projectActive):hover {
					bottom: 10px;
					box-shadow: 15px 15px 10px #ccc;
				}
				.projectCard.projectActive {
					cursor: unset;
					margin: 0;
					width: calc(100% - 200px);
					height: calc(100vh - ${global.constants.headerHeight + 200}px);
					border-radius: 30px 20px 20px 30px;
					position: fixed;
					bottom: 100px;
					left: 100px;
					box-shadow: unset;
					z-index: 3;
				}
				.projectCard.projectActive .projectImg {
					position: absolute;
					left: 0px;
					bottom: 0px;
					border-radius: 20px 0 0 20px;
					width: unset;
					height: calc(100vh - ${global.constants.headerHeight + 200}px);
				}
				.projectCard.projectActive .projectTitle {
					font-size: 42px;
					top: 5px;
					left: calc(100vh - ${global.constants.headerHeight + 185}px);
				}
				.projectCard.projectActive .projectDesc {
					display: inline-block;
					position: absolute;
					width: calc(calc(calc(100vw - 200px) - calc(100vh - ${global.constants.headerHeight + 200}px)) - 100px);
					left: calc(100vh - ${global.constants.headerHeight + 200}px);
					top: 80px;
					font-size: 24px;
					margin: 30px 50px;
				}
				.projectCard.projectActive .projectInfoLabel {
					position: relative;
					left: 50px;
				}
				.projectFade {
					display: block;
					position: fixed;
					z-index: 1;
					left: 0;
					top: 0;
					width: 100vw;
					height: 100vh;
					background-color: #00000099;
				}
			}

			@media only screen and (max-width: ${global.constants.mobileWidth}px) {
				.projectContainer {
					display: block;
					padding: 0px;
					margin: 0px;
					width: 100%;
					padding-top: 0px;
				}
				.projectCard {
					cursor: unset;
					transition: unset;
					margin: 0px;
					width: 100%;
					display: block;
					border-radius: 0px;
					background: none;
					box-shadow: unset;
					height: unset;
					padding-top: 10px;
					padding-left: 50px;
					padding-right: 50px;
				}
				.projectCard:first-child {
					padding-top: 80px;
				}
				.projectCard:last-child {
					padding-bottom: 100px;
				}
				.projectCard:hover {
					bottom: unset;
					box-shadow: unset;
				}
				.projectCard:not(:last-child) .projectSeparator {
					display: block;
					height: 1px;
					margin-top: 100px;
					margin-bottom: 60px;
					background-color: #aaa;
				}
				.projectTitle {
					font-size: 36px;
				}
				.projectImg {
					width: 100%;
					max-width: 300px;
					position: static;
					border-radius: 15px;
				}
				.projectDesc {
					display: block;
					padding: 25px 10px 0px 10px;
				}
				.projectButtonContainer {
					flex-wrap: wrap;
				}
				.projectButton {
					min-width: 200px;
				}
				.projectButtonDisabled {
					display: none;
				}
			}
    	`}</style>
	</Layout>
);

export default Projects;